body {
  background-color: #F2F2F2;
  color: #4C5963; }

.max-width-panel {
  max-width: 1440px;
  margin: auto; }

.page-wrapper {
  min-height: 100%;
  margin-bottom: -56px; }
  .page-wrapper:after {
    content: "";
    display: block;
    height: 56px; }

#page-header .contact-info-panel {
  margin-bottom: 1rem; }

#page-header .contact-info-label {
  display: inline-block;
  vertical-align: middle;
  margin-right: 1rem; }

#page-header .logo-title-block {
  padding: 0 1rem; }

#page-header .site-logo {
  max-height: 22vw;
  display: inline-block;
  vertical-align: middle; }

#page-header .site-title {
  border-left: 1px dashed;
  display: inline-block;
  font-size: 11vw;
  font-weight: 900;
  line-height: 1;
  margin: 0 0 0 0.5rem;
  padding: 0 0 0 0.5rem;
  text-transform: uppercase;
  vertical-align: middle; }

@media only screen and (min-width: 40.063em) {
  #page-header .site-logo {
    max-height: 10rem; }
  #page-header .site-title {
    font-size: 5rem; } }

@media only screen and (min-width: 64.063em) {
  #page-header .contact-info-panel {
    margin-bottom: 0; } }

#page-header .top-bar {
  margin-top: 1rem;
  border-top: 1px dashed; }
  @media only screen and (min-width: 40.063em) {
    #page-header .top-bar .first-menu-item {
      padding-left: 1rem; } }

.header-border {
  border-bottom: 1px dashed; }

#page-footer {
  height: 56px;
  overflow: hidden;
  line-height: 1.2;
  background-color: #E0E0E0; }
  #page-footer .copyright-notice {
    padding: 1rem;
    letter-spacing: 2px;
    word-spacing: 5px;
    text-transform: uppercase; }
  @media only screen and (min-width: 40.063em) {
    #page-footer {
      line-height: 1.5; } }
